<template>
<div>
	<div class="text item">
		<div class="clearfix relative">
			<!-- <div class="columns pull-left"><el-button type="primary" size="small"></el-button></div> -->
			<div class="columns pull-left">
				<el-input placeholder="" v-model="search" class="input-with-select">
					<el-button slot="append" icon="el-icon-search"></el-button>
				</el-input>
			</div>
			<div class="table-func pull-right">
				<el-button type="success" size="mini" round @click="addfield_rawDataAsset" class="pull-right">
					<i class="icon-add"></i>
					<font></font>
				</el-button>
			</div>
		</div>
		<el-form :model="rawDataAssetForm" ref="rawDataAssetForm">
		  <el-table :data="rawDataAssetForm.inspectionList.filter(data => !search || data.DECRIPTION.toLowerCase().includes(search.toLowerCase()))" row-key="ID" border stripe height="280" highlight-current-row="highlight-current-row" style="width: 100%;" :default-sort="{prop:'rawDataAssetForm.inspectionList', order: 'descending'}" v-loadmore="loadMore">
		  	<el-table-column label="" width="120" prop="P_NUM">
		      <template slot-scope="scope">
		        <el-form-item :prop="'inspectionList.'+scope.$index + '.P_NUM'" :rules="{required: true, message: '', trigger: 'blur'}">
		        	<el-input v-if="scope.row.isEditing" size="small" v-model="scope.row.P_NUM" disabled></el-input><span v-else="v-else">{{scope.row.P_NUM}}</span>
				</el-form-item>
		      </template>
		    </el-table-column>

		  	<el-table-column label="" width="160" prop="NUM">
		      <template slot-scope="scope">
		        <el-form-item :prop="'inspectionList.'+scope.$index + '.NUM'" :rules="{required: true, message: '', trigger: 'blur'}">
		        	<el-input v-if="scope.row.isEditing" size="small" v-model="scope.row.NUM" placeholder="" disabled></el-input><span v-else="v-else">{{scope.row.NUM}}</span>
				</el-form-item>
		      </template>
		    </el-table-column>

		    <el-table-column label="" sortable prop="DECRIPTION">
		      <template slot-scope="scope">
		        <el-form-item :prop="'inspectionList.'+scope.$index + '.DECRIPTION'" :rules="{required: true, message: '', trigger: 'blur'}">
		        	<el-input v-if="scope.row.isEditing" size="small" v-model="scope.row.DECRIPTION" placeholder="" disabled>
		        		<el-button slot="append" icon="icon-search" @click="addprobtn(scope.row)"></el-button>
		        	</el-input><span v-else="v-else">{{scope.row.DECRIPTION}}</span>
				</el-form-item>
		      </template>
		    </el-table-column>

			<!-- <el-table-column prop="STATUS" label="" sortable width="100" :formatter="judge">
		      <template slot-scope="scope">
		         <el-input v-if="scope.row.isEditing" size="small" v-model="scope.row.STATUS" disabled></el-input><span v-else="v-else">{{scope.row.STATUS}}</span>
		      </template>
		    </el-table-column> -->
			
			<!-- <el-table-column prop="VERSION" label="" sortable width="120">
		      <template slot-scope="scope">
		       	<el-input v-if="scope.row.isEditing" size="small" v-model="scope.row.VERSION" disabled></el-input><span v-else="v-else">#r{{scope.row.VERSION}}</span>
		      </template>
		    </el-table-column> -->

		    <!-- <el-table-column prop="CHANGEBY" label="" sortable width="120">
		      <template slot-scope="scope">
		        <el-input v-if="scope.row.isEditing" size="small" v-model="scope.row.CHANGEBY" placeholder="" disabled></el-input><span v-else="v-else">{{scope.row.CHANGEBY}}</span>
		      </template>
		    </el-table-column>

		     <el-table-column prop="CHANGEDATE" label="" sortable width="160" :formatter="dateFormat">
		      <template slot-scope="scope">
		      	<el-input v-if="scope.row.isEditing" size="small" v-model="scope.row.CHANGEDATE" disabled></el-input><span v-else="v-else">{{scope.row.CHANGEDATE}}</span>
		      </template>
		    </el-table-column> -->

		    <!-- <el-table-column prop="operationChild" fixed="right" align="center" label="" width="80">
			 	<template slot-scope="scope">
				 	<el-button type="text" id="Edit" size="medium" @click.native.prevent="saveRow(scope.row)" v-if="scope.row.isEditing">
			        	<i class="icon-check" title=""></i>
					</el-button>

				 	<el-button type="primary" round size="mini" @click="addchildRow(scope.row)" v-else="v-else">
			          
			        </el-button>
			    </template>
			 </el-table-column> -->
				<!-- <el-button type="text" size="medium" @click.native.prevent="modifyversion(scope.row)">
		        	<i class="icon-edit" title=""></i>
				</el-button> -->
			 <el-table-column prop="iconOperation" fixed="right" label="" width="80">
		      <template slot-scope="scope">
		        <el-button type="text" id="Edit" size="medium" @click.native.prevent="saveRow(scope.row)" v-if="scope.row.isEditing">
		        	<i class="icon-check" title=""></i>
				</el-button>

		        <el-button @click="deleteRow(scope.row)" type="text" size="medium" title="" v-else="v-else">
		          <i class="icon-trash red"></i>
		        </el-button>
		      </template>
		    </el-table-column>
		  </el-table>
		</el-form>
		<!--  Begin-->
		<el-pagination background class="text-right pt10 pb10"
            @size-change="sizeChange"
            @current-change="currentChange"
            :current-page="page.currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="page.pageSize"
            layout="total, sizes, prev, pager, next"
            :total="page.totalCount">
        </el-pagination>
		<!--  End-->
	</div>
	<!--  Begin -->
		<el-dialog :modal-append-to-body="false" title="" height="300px" :visible.sync="dialogVisible3" width="80%" :before-close="handleClose">
			<!--  Begin-->
			<el-table :header-cell-style="rowClass" :data="categoryList" border stripe height="300px" style="width: 100%;" :default-sort="{prop:'categoryList', order: 'descending'}" @selection-change="SelChange" v-loadmore="loadMore">
				<el-table-column type="selection" fixed width="55" align="center">
				</el-table-column>
				<el-table-column label="" width="125" sortable prop="ASSETNUM">
				</el-table-column>
				<el-table-column label="" sortable prop="DESCRIPTION">
				</el-table-column>
				<el-table-column label="" width="100" sortable prop="VERSION" align="right">
				</el-table-column>
				<el-table-column label="" width="120" prop="ENTERDATE" sortable :formatter="dateFormat">
				</el-table-column>
				<el-table-column label="" width="120" prop="CHANGEDATE" sortable :formatter="dateFormat">
				</el-table-column>
			</el-table>
			
			<!--  End-->
			<span slot="footer" class="dialog-footer">
		       <el-button type="primary" @click="addproclass"> </el-button>
		       <el-button @click="dialogVisible3 = false"> </el-button>
		    </span>
		</el-dialog>
		<!--  End -->
</div>
</template>
<script>
	import Config from '../../config.js'
	export default {
		name: 'rawDataAsset',
		components: {
		
		},
		props: ['parentIds'],
		data() {
			return {
				basic_url: Config.dev_url,
				rawDataAssetForm:{
					inspectionList: []
				},
				fullHeight: document.documentElement.clientHeight - 210+'px',//
					departmentId: '',//
					categoryList:[],//
					catedata:'',//table
					dialogVisible3: false, //
					selData:[],
				isEditing: '',
				loadSign:true,//
				commentArr:{},//
				value: '',
				options: [{
					value: '1',
					label: ''
				}, {
					value: '0',
					label: ''
				}],
				searchData: {
			        page: 1,
			        limit: 10,//
			        enabled: '',//
		        },
				search: '',//
				page: {//
					currentPage: 1,
					pageSize: 10,
					totalCount: 0
				},
				parentId: 1
			}
		},
		methods: {
			iconOperation(row, column, cell, event){//Table-
				if(column.property ==="iconOperation"){
					row.isEditing = !row.isEditing
				}
			},
			modifyversion (row) {//
				 this.$axios.get(this.basic_url + '/api-user/users/currentMap',{}).then((res)=>{
					row.CHANGEBY=res.data.nickname;
					var date=new Date();
					row.CHANGEDATE = this.$moment(date).format("YYYY-MM-DD  HH:mm:ss");
					//console.log(row);
					
				}).catch((err)=>{
					this.$message({
						message:'',
						type:'error'
					})
				})
			},
			loadMore () {//
			   if (this.loadSign) {
			     this.loadSign = false
			     this.page.currentPage++
			     if (this.page.currentPage > Math.ceil(this.page.totalCount/this.page.pageSize)) {
			       return
			     }
			     setTimeout(() => {
			       this.loadSign = true
			     }, 1000)
			     this.viewfield_rawDataAsset(this.selParentId,this.parentId);
			   }
			 },
			 addprobtn(row){///
				this.catedata = row;//table
				this.dialogVisible3 = true;
				var data = {
					page: this.page.currentPage,
					limit: this.page.pageSize,
				};
				this.$axios.get(this.basic_url + '/api-apps/app/asset?DEPTID=' + this.parentIds, {
					params: data
				}).then((res) => {
					// console.log(this.parentIds);
					// console.log(res.data);
					this.page.totalCount = res.data.count;
					//
					let totalPage = Math.ceil(this.page.totalCount / this.page.pageSize)
					if(this.page.currentPage >= totalPage) {
						this.loadSign = false
					} else {
						this.loadSign = true
					}
					this.commentArr[this.page.currentPage] = res.data.data
					let newarr = []
					for(var i = 1; i <= totalPage; i++) {
						if(typeof(this.commentArr[i]) != 'undefined' && this.commentArr[i].length > 0) {
							for(var j = 0; j < this.commentArr[i].length; j++) {
								newarr.push(this.commentArr[i][j])
							}
						}
					}
					this.categoryList = newarr;
				}).catch((wrong) => {})
			},
			sizeChange(val) {//
		      this.page.pageSize = val;
		      this.viewfield_rawDataAsset(this.selParentId,this.parentId);
		    },
		    currentChange(val) {//
		      this.page.currentPage = val;
		      this.viewfield_rawDataAsset(this.selParentId,this.parentId);
		    },
			searchinfo(index) {
				this.page.currentPage = 1;
				this.page.pageSize = 10;
				this.viewfield_rawDataAsset(this.selParentId,this.parentId);
			},
			judge(data) {//taxStatus 
				return data.enabled ? '' : ''
			},
			//  
			dateFormat(row, column) {
				var date = row[column.property];
				if(date == undefined) {
					return "";
				}
				return this.$moment(date).format("YYYY-MM-DD");
			},
			indexMethod(index) {
				return index + 1;
			},
			viewfield_rawDataAsset(id,num){//
				if(id=='null'){
					this.rawDataAssetForm.inspectionList = []; 
					return false;
					//todo  
				}
				this.parentId = num;
				var url = this.basic_url + '/api-apps/app/rawDataAsset/INSPECTION_PROJECT2/' + id;
				this.$axios.get(url, {}).then((res) => {
					// console.log(res);
					this.page.totalCount = res.data.count;	
					//
					let totalPage=Math.ceil(this.page.totalCount/this.page.pageSize)
					if(this.page.currentPage >= totalPage){
						 this.loadSign = false
					}else{
						this.loadSign=true
					}
					this.rawDataAssetForm.inspectionList=res.data.RAW_DATA_ASSETList;
					
					for(var j = 0; j < this.rawDataAssetForm.inspectionList.length; j++){
						this.rawDataAssetForm.inspectionList[j].isEditing = false;
					}

					this.$refs.singleTable.setCurrentRow(this.rawDataAssetForm.inspectionList[0]);//

				}).catch((wrong) => {})
			},
			requestData_rawDataAsset(index) {//
				var data = {
					page: this.page.currentPage,
					limit: this.page.pageSize,
				}
				var url = this.basic_url + '/api-apps/app/rawDataAsset';
				this.$axios.get(url, {
					params: data
				}).then((res) => {
					this.page.totalCount = res.data.count;	
					//
					let totalPage=Math.ceil(this.page.totalCount/this.page.pageSize)
					if(this.page.currentPage >= totalPage){
						 this.loadSign = false
					}else{
						this.loadSign=true
					}
					this.commentArr[this.page.currentPage]=res.data.data
					let newarr=[]
					for(var i = 1; i <= totalPage; i++){
					
						if(typeof(this.commentArr[i])!='undefined' && this.commentArr[i].length>0){
							
							for(var j = 0; j < this.commentArr[i].length; j++){
								this.commentArr[i][j].isEditing = false;
								newarr.push(this.commentArr[i][j])
							}
						}
					}
					
					this.rawDataAssetForm.inspectionList = newarr;
				}).catch((wrong) => {})
			},
			//
			SelChange(val) {
				this.selData = val;
			},
			//
			rowClass({ row, rowIndex}) {
			    return 'text-align:center'
			},
			handleClose(done) {//
				this.$confirm('')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			formatter(row, column) {
				return row.enabled;
			},
			addfield_rawDataAsset(P_NUM) { //Table
				if (this.parentId == 1) {
					this.$message({
						message:'/',
						type:'warning'
					})
				} else {
					var isEditingflag=false;
					for(var i=0;i<this.rawDataAssetForm.inspectionList.length; i++){
						if (this.rawDataAssetForm.inspectionList[i].isEditing==false){
							isEditingflag=false;
						}else{
	                        isEditingflag=true;
	                        break;
						}
					}
					if (isEditingflag==false){
	                	this.$axios.get(this.basic_url + '/api-user/users/currentMap',{}).then((res)=>{
	                		var currentUser, currentDate
							this.currentUser=res.data.nickname;
							var date=new Date();
							this.currentDate = this.$moment(date).format("YYYY-MM-DD  HH:mm:ss");
							var obj = {
								"P_NUM": this.parentId,
								"DECRIPTION": '',
								"STATUS": '',
								"NUM": '',
								"VERSION": '',
								"DEPTID": '',
								"CHANGEBY": this.currentUser,
								"CHANGEDATE": this.currentDate,
								"isEditing": true,
							};
							this.rawDataAssetForm.inspectionList.unshift(obj);//unshiftpush
						}).catch((err)=>{
							this.$message({
								message:'',
								type:'error'
							})
						})
		            } else {
		                this.$message.warning("");
					}
				}
			},
			saveRow (row) {//Table-
				this.$refs['rawDataAssetForm'].validate((valid) => {
					row.VERSION = row.VERSION + 1;//+1
		          if (valid) {
					var url = this.basic_url + '/api-apps/app/rawDataAsset/saveOrUpdate';
					var submitData = {
						"ID":row.ID,
						"P_NUM": row.P_NUM,
					    "NUM": row.NUM,
						"DECRIPTION": row.DECRIPTION,
						"STATUS": row.STATUS,
					    "VERSION": row.VERSION,
					    "DEPTID": row.DEPTID,
						"CHANGEBY": row.CHANGEBY,
					    "CHANGEDATE": row.CHANGEDATE,
					}
					this.$axios.post(url, submitData).then((res) => {
						if(res.data.resp_code == 0) {
							this.$message({
								message: '',
								type: 'success'
							});
							//
							// this.requestData_rawDataAsset();
							this.viewfield_rawDataAsset(this.selParentId,this.parentId);
						}
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});
		          } else {
		            return false;
		          }
		        });
			},
			deleteRow(row) {//Table-
				this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                }).then(({ value }) => {
                	var url = this.basic_url + '/api-apps/app/rawDataAsset/' + row.ID;
                    this.$axios.delete(url, {}).then((res) => {//.delete 
					//resp_code == 0 
						if(res.data.resp_code == 0) {
							this.$message({
								message: '',
								type: 'success'
							});
							this.viewfield_rawDataAsset(this.selParentId,this.parentId);
						}
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});
                }).catch(() => {

            	});
			},
			addproclass() { //
				this.dialogVisible3 = false
				this.catedata.NUM = this.selData[0].ASSETNUM;
				this.catedata.DECRIPTION = this.selData[0].DESCRIPTION;
				this.catedata.DEPTID = this.selData[0].DEPTID;
				this.catedata.VERSION = this.selData[0].VERSION;
				this.$emit('request');
			},
		},
		
		// mounted() {
		// 	this.requestData_rawDataAsset();
		// },
		

	}
</script>

<style scoped>
.el-form-item__error {
	top: 18%;
    left: 5px;
    background: #FFF;
    padding: 5px 10px;
}

.table-func {
	position: absolute;
	top: -50px;
    right: 0px;
    z-index: 888;
}
.columns{
	width:220px;
	position: absolute;
    right: 100px;
    bottom: -40px;
    z-index: 88;
}
</style>


